<template>
    <view class="container">
        <image src="/static/logo.png" class="logo" />
        <button @click="wxLogin" class="wx-login-btn">微信登录</button>
    </view>
</template>

<script>
export default {
    methods: {
        wxLogin() {
            uni.login({
                provider: 'weixin',
                success: loginRes => {
                    console.log(loginRes.code);
                    this.sendCodeToServer(loginRes.code);
                },
                fail: err => {
                    console.error("微信登录失败：", err);
                    uni.showToast({
                        title: '微信登录失败',
                        icon: 'none'
                    });
                }
            });
        },
        sendCodeToServer(code) {
            uni.request({
                url: '192.168.1.25:10012/api/user/login',
                method: 'POST',
                data: {
                    code: code
                },
                header: {
                    'content-type': 'application/json' // 自定义请求头信息
                },
                success: res => {
                    if (res.data.status === 'success') {
                        // 如果成功，保存从后端返回的用户信息或token等
                        uni.setStorageSync('token', res.data.token);
                        uni.showToast({
                            title: '登录成功',
                            icon: 'success'
                        });
                        uni.switchTab({
                            url: '/pages/index/index' // 假设你的主页路由是这个
                        });
                    } else {
                        uni.showToast({
                            title: res.data.message || '登录失败',
                            icon: 'none'
                        });
                    }
                },
                fail: err => {
                    console.error("请求后端失败：", err);
                    uni.showToast({
                        title: '服务器请求失败',
                        icon: 'none'
                    });
                }
            });
        }
    }
};
</script>

